import {createApi, createStore} from 'effector';
import {SessionPlaceholderModel} from "../session";
import {isArray} from "../../../helpers/isArray";
import {isObject} from "../../../helpers/isObject";

export type Placeholders = Dict<string>;
const $placeholders = createStore<Placeholders>({});

const {sdkPlaceholdersChanged, addDefaultPlaceholders} = createApi($placeholders, {
  sdkPlaceholdersChanged: (state: Placeholders, data: Placeholders) => data,
  addDefaultPlaceholders: (state: Placeholders, data: Array<SessionPlaceholderModel>) => {
    let _placeholders: Placeholders = {};
    if (isArray(data)) {
      data.forEach(item => {
        if (isObject(item) && item['name'] !== undefined && item['default_value'] !== undefined) {
          _placeholders[item['name']] = item['default_value'];
        }
      });
    }
    return _placeholders;
  }
});

export {$placeholders, sdkPlaceholdersChanged, addDefaultPlaceholders};
