import {createDomain, Effect} from 'effector';
import {
  ClickOnStoryInternalPayload,
  ClickOnStoryPayload,
  FontResource,
  STORY_LIST_TYPE
} from "../story-manager/common.h";
import {Dict} from "../../global.h";

// use name|sid or  "effector/babel-plugin"
const toServerFxDomain = createDomain("serverFxDomainWidgetStoriesList");
const toClientFxDomain = createDomain("clientFxDomainWidgetStoriesList");

export {toServerFxDomain as serverFxDomain, toClientFxDomain as clientFxDomain}

export const rpcDomains = {
  serverFxDomain: toServerFxDomain,
  clientFxDomain: toClientFxDomain
}

// export const workerMessage = domain.effect<string, string[]>();
// export const failure = domain.effect<void, any>();
// export const ping: Effect<{now: number}, {ping: number}> = domain.effect();



export const RpcMessage = toClientFxDomain.effect<string, string[]>("RpcMessage");
export const setStories = toClientFxDomain.effect<{listType: STORY_LIST_TYPE, storyList: Array<Dict>}, void>("setStories");
export const setFonts = toClientFxDomain.effect<FontResource, void>("setFonts");

export const RpcMessageOnServer = toServerFxDomain.effect<string, string[]>("RpcMessageOnServer");


export const widgetLoaded = toServerFxDomain.effect<void, void>("widgetLoaded");
export const clickOnStoryInternalEvent = toServerFxDomain.effect<ClickOnStoryInternalPayload, void>("clickOnStoryInternalEvent");
export const flushThumbViewsEvent = toServerFxDomain.effect<Array<number>, void>("flushThumbViewsEvent");
export const clickOnStoryEvent = toServerFxDomain.effect<ClickOnStoryPayload, void>("clickOnStoryEvent");
export const clickOnStoryDeepLinkEvent = toServerFxDomain.effect<ClickOnStoryPayload, void>("clickOnStoryDeepLinkEvent");


