import './storyManagerConfig/init';
import './session/init';
import './story/init';

import API, {ApiRequestConfig} from './../api';
import {createApi, createEffect, Event, forward, sample} from "effector";
import {$session, SessionModel} from "./session";
import {getRequestConfig as sessionGetRequestConfig} from "./session/init";
import {getRequestConfig as storyContextGetRequestConfig} from "./storyContext/init";
import {
  getRequestConfig as storyGetRequestConfig,
  storySlidesGetRequestConfig
} from "./story/init";
import {$stories, $storiesIds, storyCheckLocalIsOpened, StoryModel} from "./story";
import {$storyContext, StoryContextModel} from "./storyContext";
import {replaceAll} from "../../stories_widget/util/string";
import {$placeholders} from "./placeholder";
import {STORY_LIST_TYPE} from "../types";
import {Option, Dict} from "../../../global.h";
import {isEmpty} from "../../helpers/isEmpty";
import {isObject} from "../../helpers/isObject";


type CompositeResponseData<T = Dict<any>> = {
  success: boolean,
  compositeResponse: Array<{
    ref: string,
    body: T
  }>,
};


const fetchSessionAndStoriesCompositeFx = createEffect<{ defaultList: boolean, favoriteList: boolean, onboardingList: boolean, onboardingTags?: Array<string> },
  Array<Dict>,
  Error>(async ({defaultList, onboardingList, favoriteList, onboardingTags}): Promise<Array<Dict>> => {

  // TODO custom tags for each list requests

  const compositeRequest: Array<CompositeSubRequestData> = [];

  const sessionId = $session.getState()?.session?.id;
  let compositeSessionIdRef: Option<string> = null;
  if (isEmpty(sessionId)) {
    compositeRequest.push(getCompositeSubRequestDataFromRequestConfig(await sessionGetRequestConfig(), "session"));
    compositeSessionIdRef = "@{session.session.id}";
  }

  if (defaultList) {
    compositeRequest.push(getCompositeSubRequestDataFromRequestConfig(storyGetRequestConfig(STORY_LIST_TYPE.default, compositeSessionIdRef), "storyList"));
  }

  if (favoriteList) {
    compositeRequest.push(getCompositeSubRequestDataFromRequestConfig(storyGetRequestConfig(STORY_LIST_TYPE.favorite, compositeSessionIdRef), "storyFavoriteList"));
  }

  if (onboardingList) {
    compositeRequest.push(getCompositeSubRequestDataFromRequestConfig(storyGetRequestConfig(STORY_LIST_TYPE.onboarding, compositeSessionIdRef, onboardingTags), "storyOnboardingList"));
  }

  // todo try catch
  const response = (await API.post<CompositeResponseData<Array<StoryModel>>>('composite', {compositeRequest}));

  // console.log({data: response.data});

  if (response.data && response.data.success && response.data.compositeResponse && Array.isArray(response.data.compositeResponse)) {
    return response.data.compositeResponse;
  }
  return [];
});

