type CompositeSubRequestData =
  Pick<ApiRequestConfig, "method">
  & { path?: string, qs?: Option<Dict>, body?: Option<Dict>, ref: string };

const getCompositeSubRequestDataFromRequestConfig = (config: ApiRequestConfig, ref: string): CompositeSubRequestData => {
  const requestData: CompositeSubRequestData = {path: config.url, qs: config.params, body: config.data, ...config, ref};
  return requestData;
}


const replacePlaceholders = (value: string): string => {
  for (let placeholdersKey in $placeholders.getState()) {
    if ($placeholders.getState().hasOwnProperty(placeholdersKey)) {
      value = replaceAll(value, `%${placeholdersKey}%`, $placeholders.getState()[placeholdersKey])
    }
  }
  return value;
};

const processStories = (list: Array<StoryModel> | undefined) => Array.isArray(list) ? list.map(item => ({
  ...item,
  title: item.need_placeholders ? replacePlaceholders(item.title) : item.title,
  slidesLoaded: false,
  is_opened: storyCheckLocalIsOpened(item)
} as StoryModel)) : [];

// продумать как проверять наличие полей в моделях
forward({
  from: fetchSessionAndStoriesCompositeFx.doneData.filterMap(list => list.find(item => item.ref === "session")?.body),
  to: $session
});

sample({
  source: $stories,
  clock: $session,
  fn: (source, clock) => source.map(item => ({...item, is_opened: storyCheckLocalIsOpened(item, clock.user_key)})),
  target: $stories
});

type StoriesListChangedEvent = {
  listType: STORY_LIST_TYPE,
  storyList: Array<StoryModel>
};
const {storiesListItemsAdded} = createApi($stories, {
    storiesListItemsAdded: (state, {listType, storyList}: StoriesListChangedEvent) => {
      const ids = state.map(_ => _.id);
      return [...state, ...storyList.filter(item => ids.indexOf(item.id) === -1)];
    },

  }
);
const {storiesListIdsChanged} = createApi($storiesIds, {
  storiesListIdsChanged: (state, {listType, storyList}: StoriesListChangedEvent) => {
    const ids = new Map(state);
    ids.set(listType, storyList.map(item => item.id));
    return ids;
  },
});

forward({
  from: fetchSessionAndStoriesCompositeFx.doneData.filterMap(list => {
    const storyList = processStories(list.find(item => item.ref === "storyList")?.body);
    if (storyList.length > 0) {
      return {
        listType: STORY_LIST_TYPE.default,
        storyList
      };
    }
    return undefined;
  }),
  to: [storiesListItemsAdded, storiesListIdsChanged]
});
forward({
  from: fetchSessionAndStoriesCompositeFx.doneData.filterMap(list => {
    const storyList = processStories(list.find(item => item.ref === "storyFavoriteList")?.body);
    if (storyList.length > 0) {
      return {
        listType: STORY_LIST_TYPE.favorite,
        storyList
      };
    }
    return undefined;
  }),
  to: [storiesListItemsAdded, storiesListIdsChanged]
});
forward({
  from: fetchSessionAndStoriesCompositeFx.doneData.filterMap(list => {
    // undefined - to prevent store trigger
    const storyList = processStories(list.find(item => item.ref === "storyOnboardingList")?.body);

    if (storyList.length > 0) {
      return {
        listType: STORY_LIST_TYPE.onboarding,
        storyList
      };
    }
    return undefined;
  }),
  to: [storiesListItemsAdded, storiesListIdsChanged]
});


const fetchStoriesSlidesCompositeFx = createEffect<{ ids: Array<number> }, Array<{ body: StoryModel }>, Error>();

fetchStoriesSlidesCompositeFx.use(async (params): Promise<Array<{ body: StoryModel }>> => {
  const compositeRequest = params.ids.map(id => getCompositeSubRequestDataFromRequestConfig(storySlidesGetRequestConfig(id), `storySlides_${id}`));

  // todo try catch
  const response = await API.post<CompositeResponseData<StoryModel>>('composite', {compositeRequest});

  // console.log({data: response.data});

  if (response.data && response.data.success && response.data.compositeResponse && Array.isArray(response.data.compositeResponse)) {
    return response.data.compositeResponse;
  }
  return [];
});

const isStoryModel = (x: any): x is StoryModel => x && isObject(x) && ("id" in x);
const storySlidesReducer = (state: Array<StoryModel>, data: Array<{ body: StoryModel | StoryContextModel }>): Array<StoryModel> => {
  if (Array.isArray(data) && Array.isArray(state)) {
    return state.map(item => {
      for (let i = 0; i < data.length; i++) {
        // @ts-ignore
        if (isStoryModel(data[i]?.body) && data[i]?.body?.id === item.id) {
          return Object.assign({}, item, {
            // @ts-ignore
            slides_html: item.need_placeholders ? Array.from(data[i]?.body?.slides_html).map(item => replacePlaceholders(item)) : Array.from(data[i]?.body?.slides_html),
            // @ts-ignore
            slides_duration: data[i]?.body?.slides_duration,
            slidesLoaded: true,
            is_opened: storyCheckLocalIsOpened(item),
          });
        }
      }
      return item;
    });
  }
  return state;
};


const fetchStoriesContextAndStorySlidesCompositeFx = createEffect<{ ids: Array<number>, id: number }, Array<{ ref: string, body: StoryModel | StoryContextModel }>, Error>();
fetchStoriesContextAndStorySlidesCompositeFx.use(async ({
                                                          ids,
                                                          id
                                                        }): Promise<Array<{ ref: string, body: StoryModel | StoryContextModel }>> => {
  const compositeRequest = [
    getCompositeSubRequestDataFromRequestConfig(storySlidesGetRequestConfig(id), 'storySlides'),
    getCompositeSubRequestDataFromRequestConfig(storyContextGetRequestConfig(ids), 'storyContext'),
  ];

  // todo try catch
  const response = await API.post<CompositeResponseData<StoryModel|StoryContextModel>>('composite', {compositeRequest});

  // console.log({data: response.data});

  if (response.data && response.data.success && response.data.compositeResponse && Array.isArray(response.data.compositeResponse)) {
    return response.data.compositeResponse;
  }
  return [];
});

// slides
$stories.on<any>([fetchStoriesSlidesCompositeFx.doneData, fetchStoriesContextAndStorySlidesCompositeFx.doneData], storySlidesReducer);

forward({
  from: fetchStoriesContextAndStorySlidesCompositeFx.done.filterMap(list => {
    const context = list.result.find(item => item.ref === "storyContext")?.body;
    if (context && isObject(context)) {
      return {...context, ids: list.params.ids};
    }
    return undefined;
  }) as Event<Option<StoryContextModel>>,
  to: $storyContext
});

