/**
 * For load session, context, slides
 */
const fetchSessionAndStoryDataCompositeFx = createEffect<{ id: number | string}, Array<{ ref: string, body: SessionModel | StoryModel | StoryContextModel }>,
  Error>(async ({id}): Promise<Array<{ ref: string, body: SessionModel | StoryModel | StoryContextModel }>> => {

  const compositeRequest: Array<CompositeSubRequestData> = [];

  const sessionId = $session.getState()?.session?.id;
  let compositeSessionIdRef: Option<string> = null;
  if (isEmpty(sessionId)) {
    compositeRequest.push(getCompositeSubRequestDataFromRequestConfig(await sessionGetRequestConfig(), "session"));
    compositeSessionIdRef = "@{session.session.id}";
  }

  compositeRequest.push(getCompositeSubRequestDataFromRequestConfig(storySlidesGetRequestConfig(id, true, compositeSessionIdRef), `story`));
  compositeRequest.push(getCompositeSubRequestDataFromRequestConfig(storyContextGetRequestConfig([id], compositeSessionIdRef), 'storyContext'));

  // todo try catch
  const response = await API.post<CompositeResponseData<StoryModel|SessionModel|StoryContextModel>>('composite', {compositeRequest});

  // console.log({data: response.data});

  if (response.data && response.data.success && response.data.compositeResponse && Array.isArray(response.data.compositeResponse)) {
    return response.data.compositeResponse;
  }
  if (response.data && !response.data.success) {
    // todo 404 - part
    // each - find error
    throw Error('composite request error:' + JSON.stringify(response.data.compositeResponse));
  }


  return [];
});

const isSessionModel = (item: any): item is SessionModel => {
  return !!item
}
forward({
  from: fetchSessionAndStoryDataCompositeFx.doneData.filterMap(list => {
    const sessionModel = list.find(item => item.ref === "session")?.body;
    if (sessionModel && isObject(sessionModel) && isSessionModel(sessionModel)) {
      return sessionModel;
    }
    return undefined;
  }),
  to: $session
});

forward({
  from: fetchSessionAndStoryDataCompositeFx.done.filterMap(list => {
    const context = list.result.find(item => item.ref === "storyContext")?.body;
    if (context && isObject(context)) {
      // ids from back
      return {...context, ids: [list.params.id]};
    }
    return undefined;
  }) as Event<Option<StoryContextModel>>,
  to: $storyContext
});

// slides
$stories.on(fetchSessionAndStoryDataCompositeFx.doneData.filterMap(list => {
  const story = list.find(item => item.ref === "story")?.body;
  if (story && isObject(story)) {
    return story;
  }
  return undefined;
}), (state: Array<StoryModel>, data): Array<StoryModel> | undefined => {
  if (!isStoryModel(data)) {
    return undefined;
  }

  const newState = state.filter(_ => _.id !== (data as any)?.id).slice(0);
  data = Object.assign(data, {
    // @ts-ignore
    slides_html: data.need_placeholders ? Array.from(data.slides_html).map(item => replacePlaceholders(item)) : Array.from(data.slides_html),
    // @ts-ignore
    slides_duration: data.slides_duration,
    slidesLoaded: true,
    is_opened: storyCheckLocalIsOpened(data),
  });

  newState.push(data);
  return newState;
});



export {
  fetchSessionAndStoriesCompositeFx,
  fetchStoriesSlidesCompositeFx,
  fetchStoriesContextAndStorySlidesCompositeFx,
  storiesListIdsChanged,
  fetchSessionAndStoryDataCompositeFx
};







