import {createApi, createEffect, createStore} from 'effector';
import {STORY_LIST_TYPE} from "../../types";
import {$session} from "../session";
import {StoryManager} from "../../index";
import {isArray} from "../../../helpers/isArray";
import {isEmpty} from "../../../helpers/isEmpty";

export type StoryModel = {
  id: number,
  title: string,
  title_color: string,
  background: string, // img/video
  background_color: string,
  image: string,
  display_to: number,
  display_from: number,
  slides_html: Array<number>, // expand
  slides_duration: Array<number>,
  favorite: boolean,
  like: boolean,
  slides_count: number,
  deeplink: string,
  hide_in_reader: boolean,
  disable_close: boolean,
  like_functional: boolean,
  favorite_functional: boolean,
  share_functional: boolean,
  is_opened: boolean,
  tags: string,
  has_audio: boolean,
  video_cover: string,
  need_placeholders: boolean,
  has_swipe_up: boolean,
  sharePath: string,

  slidesLoaded: boolean


};

const $stories = createStore<Array<StoryModel>>([]);
const $storiesIds = createStore<Map<STORY_LIST_TYPE, Array<number>>>(new Map());

const $storiesDefault = createStore<Array<StoryModel>>([]);
const $storiesFavorite = createStore<Array<StoryModel>>([]);
const $storiesOnboarding = createStore<Array<StoryModel>>([]);

$storiesDefault.on<any>([$storiesIds, $stories], (state, ids) => $stories.getState().filter(item => ($storiesIds.getState().get(STORY_LIST_TYPE.default) ?? []).indexOf(item.id) !== -1));
$storiesFavorite.on<any>([$storiesIds, $stories], (state, ids) => $stories.getState().filter(item => ($storiesIds.getState().get(STORY_LIST_TYPE.favorite) ?? []).indexOf(item.id) !== -1));
// width filter by ```is_opened```
$storiesOnboarding.on<any>([$storiesIds, $stories], (state, ids) => $stories.getState().filter(item => ($storiesIds.getState().get(STORY_LIST_TYPE.onboarding) ?? []).indexOf(item.id) !== -1 && !item.is_opened));


const fetchStoriesFx = createEffect<{ listType: STORY_LIST_TYPE }, Array<StoryModel>, Error>();

// localData isOpened
// $stories.on(fetchStoriesFx.doneData, (_, stories: Array<StoryModel>) => stories);

const storyCheckLocalIsOpened = async (story: StoryModel, userKey: string = '') => {
  if (story.is_opened) {
    return story.is_opened;
  }
  const key = `u/${$session.getState().user_key}/s_opened` || userKey;
  if (!isEmpty(key)) {
    let data = await StoryManager.getInstance().localStorageGetArray<number>(key);
    if (!isArray(data)) {
      data = [];
    }
    if (data.indexOf(story.id) !== -1) {
      return true;
    }
  }
  return story.is_opened;
}

