const $storiesIdsWithoutLoadedSlides = $stories.map(items => items.filter(item => !item.slidesLoaded).map(item => item.id));

const {storyItemFavoriteChanged, storyItemLikeChanged, storyItemIsOpenedChanged} = createApi($stories, {
  storyItemFavoriteChanged: (state, {id, favorite}) => {
    const models = state.slice(0);
    const story = models.find(_ => _.id === id);
    if (story !== undefined) {
      story.favorite = favorite;
    }
    return models;
  },
  storyItemLikeChanged: (state, {id, like}) => {
    const models = state.slice(0);
    const story = models.find(_ => _.id === id);
    if (story !== undefined) {
      story.like = like;
    }
    return models;
  },
  storyItemIsOpenedChanged: (state, {id}) => {
    const models = state.slice(0);
    const story = models.find(_ => _.id === id);
    if (story !== undefined) {
      story.is_opened = true;
    }
    return models;
  },

});

