export function getRequestConfig(listType: STORY_LIST_TYPE, compositeSessionIdRef?: Option<string>, customTags?: Array<string>): ApiRequestConfig {
    let url = "story";
    if (listType === STORY_LIST_TYPE.onboarding) {
        url = "story-onboarding";
    }
    return {
        method: "get",
        url,
        ...configForListRequest(listType, compositeSessionIdRef, customTags)
    } as ApiRequestConfig;
}

export function storySlidesGetRequestConfig(id: number | string, allFields = false, compositeSessionIdRef?: Option<string>): ApiRequestConfig {
  const config: ApiRequestConfig = {
    url: `story/${id}`,
    method: "get",
    params: {expand: ['slides_html', 'slides_duration'].join(',')},
  };
  if (!allFields && config.params) {
    config.params.fields = ['id', 'slides_html', 'slides_duration'].join(',');
  }
  if (compositeSessionIdRef !== null && config.params) {
    config.params.session_id = compositeSessionIdRef;
  }
  return config;
}



fetchStoriesFx.use(async ({listType}) => API.get<StoryModel>('story', configForListRequest(listType)));

const configForListRequest = (listType: STORY_LIST_TYPE, compositeSessionIdRef?: Option<string>, customTags?: Array<string>) => {
    let tags = $storyManagerConfig.getState()?.tags;
    const params: {tags?: Option<string>, favorite?: number, session_id?: string} = {};
    if (tags && isArray(tags) && tags.length > 0) {
        params.tags = tags.join(',');
    }
    if (customTags !== undefined && isArray(customTags) && customTags.length > 0) {
      params.tags = customTags.join(',');
    }
    if (listType === STORY_LIST_TYPE.favorite) {
        params.favorite = 1;
    }

    if (compositeSessionIdRef) {
        params.session_id = compositeSessionIdRef;
    }
    return {params};
};


